/* Node Content
--------------------------------------*/
/* Page title */
.page-title-wrap {
  position: relative;
  margin: 0;
}
.page-title-wrap h1.page-title {
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-size: 2em;
  color: var(--bold-color);
}

/* Admin Tabs */
ul.page-tabs {
  display: flex;
  margin: 0 0 10px 0;
  padding: 0;
  border-bottom: 1px solid var(--border);
  list-style: none;
}
.page-tabs li {
  padding: 0;
}
.page-tabs li a {
  border-right: 2px solid #ffffff;
  padding: 4px 10px;
  background: #e2dfd8;
  color: var(--bold-color);
  transition: all 0.3s ease;
  text-shadow: 1px 1px #ffffff;
}
ul.page-tabs li.active-page-tab a,
ul.page-tabs li a:hover {
  background: var(--primary);
  color: #ffffff;
  text-shadow: none;
}

/* common for all nodes */
.node,
.node-promoted,
.node-sticky,
.node-unpublished,
.node-view-mode-full {
  position: relative;
}
.node-view-mode-teaser.node-sticky {
  position: relative;
  padding: 0 1rem 1rem 1rem;
  background: #ffffff;
  border: 1px solid var(--border);
  border-radius: 6px;
}

/* node in teaser view */
.node-view-mode-teaser {
  position: relative;
  margin-bottom: 60px;
  padding-bottom: 60px;
  border-bottom: 4px double var(--border);
}
.node-view-mode-full .node-taxonomy-container {
  margin-bottom: 14px;
}
/* node author and submitted details */
.node-header {
  position: relative;
}
.author-picture {
  float: left;
}
.author-picture img {
  width: auto;
  height: 30px;
  margin-right: 6px;
}
.node-submitted-details {
  margin: 0 0 8px 0;
  padding: 4px 0;
  color: #96918b;
  border-bottom: 1px solid var(--border);
}
.node-submitted-details i {
  color: var(--primary);
}
.node-submitted-details a {
  color: #96918b;
}
.node-submitted-details i.fa-calendar,
.node-submitted-details i.fa-th-large {
  margin-left: 14px;
}

/* node taxonomy and links */
.node-taxonomy-container,
.node-links-container {
  position: relative;
  display: block;
  width: 100%;
  margin: 1rem 0 0 0;
  padding: 1rem 0;
  border-top: 1px solid var(--border);
}
.node-links-container {
  border-bottom: 1px solid var(--border);
}
h3.term-title {
  margin: 0;
  font-size: 1.1rem;
  line-height: 1;
}
.term-title i {
  font-size: 1.1rem;
  color: var(--primary);
}
ul.taxonomy-terms {
  margin: 1rem 0 0.2rem 0;
  padding: 0;
  list-style: none;
  list-style-type: none;
}
li.taxonomy-term {
  display: inline-block;
}
li.taxonomy-term a {
  padding: 4px 12px;
  border: 3px solid #ffffff;
  border-radius: 2px;
  transition: all 0.4s ease;
}
li.taxonomy-term a:hover {
  background: #ffffff;
}
.node-links-container ul.links {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
  list-style-type: none;
}
ul.inline,
ul.links.inline {
  display: inline;
  padding-left: 0;
}
ul.inline li {
  position: relative;
  display: inline-block;
  padding: 0;
  list-style-type: none;
}
.node-links-container li {
  margin-right: 20px;
  float: left;
}
.node-links-container li.node-readmore {
  margin-left: 0;
  float: right;
}
.node-links-container .comment-comments::before {
  font-family: "FontAwesome";
  content: '\f0e6';
  padding-right: 4px;
}
.node-links-container .comment-add::before {
  font-family: "FontAwesome";
  content: '\f27b';
  padding-right: 4px;
}
.node-readmore {
  float: left;
}
li.node-readmore a {
  padding: 6px 12px;
  background: var(--primary);
  color: #ffffff;
  -webkit-transition: background 0.4s ease;
  -moz-transition: background 0.4s ease;
  transition: background 0.4s ease;
}
li.node-readmore a:hover {
  background: var(--bold-color);
  color: #ffffff;
}
li.node-readmore a::after {
  content: "\f178";
  padding-left: 10px;
  font-family: "FontAwesome";
}
.node-view-mode-teaser li.comment-add,
.node-view-mode-teaser li.comment-forbidden {
  text-align: right;
}

/* pager */
nav.pager {
  position: relative;
}
.pager ul.pager__items {
  position: relative;
  margin: 0;
  padding: 1rem 0;
  list-style: none;
  list-style-type: none;
}
.pager__items {
  clear: both;
  text-align: center;
}
.pager__item {
  display: inline-block;
}
.pager__item a {
  padding: 8px 14px;
  border: 3px solid #ffffff;
  border-radius: 3px;
}
.pager__item a:hover,
.pager__item.is-active a {
  background-color: var(--primary);
  color: #ffffff;
}
.countdownHolder, #jquery-countdown-timer-note, #block-reestraciya {
  text-align: center;
}
.block-content {
  span {
    font-size: 20px !important;
    @media (max-width: 1140px) {
      font-size: 19px !important;
    }
    @media (max-width: 991px) {
      font-size: 18px !important;
    }
    @media (max-width: 600px) {
      font-size: 16px !important;
    }
  }
}
#jquery-countdown-timer {
  span {
    font-size: 40px !important;
    @media (max-width: 600px) {
      font-size: 32px !important;
    }
    @media (max-width: 480px) {
      font-size: 22px !important;
    }
  }
}
#block-reestraciya {
  .block-content .field-item {
    p {
      a {
        font-size: 22px;
        padding: 15px 30px;
        background: antiquewhite;
        border-radius: 25px;
        border: #B3B3B3 1px solid;
        transition: all 0.5s;
      }
      a:hover {
        transition: all 1s;
        background: transparent;
      }
    }
  }
}
#block-faktitacifripromti2023 {
  h3.block-title {
    font-size: 36px;
  }
  h3 {
    font-size: 24px;
    text-align: center;
    span {
      padding: 10px 20px;
      border: 1px solid darkgrey;
      border-radius: 25px;
    }
  }
}
.views-field-field-image-committee {
  .image-field {
    height: 220px;
  }
}
.views-col {
  padding-right: 10px;
}
.views-col-5 {
  padding-right: 0;
}
.view.view-slider {
  padding: 0;
  .slick__arrow {
    display: none;
  }
  .slide__caption {
    padding-bottom: 0;
  }
}
.webform-submission-contact-form {
  max-width: 660px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  textarea {
    max-width: 660px;
  }
  input {
    max-width: 660px;
  }
  .form-item-choose-a-topic {
    select {
      border: 1px solid var(--border);
      border-radius: 4px;
      background: #ffffff;
      max-width: 696px;
      width: 100%;
    }
  }
  @media screen and (max-width: 767px) {
    textarea {
      max-width: 598px;
    }
    .form-item-choose-a-topic {
      select {
        max-width: 598px;
      }
    }
  }
}
.facts-h3 {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
}
#block-links {
  position: fixed;
  right: 3%;
  top: 40%;
  p {
    margin: 0;
  }
  #icon {
    max-width: 35px;
  }
  @media (max-width: 700px) {
    font-size: 14px;
    padding: 9px;
    right: 1%;
    #icon {
      max-width: 22px;
    }
  }
  @media (max-width: 480px) {
    display: none;
  }
}
