/* Homepage
-------------------------------------------- */
#slider {
  position: relative;
  width: 100%;
  min-height: 100vh;
  margin: 0 auto;
  background-color: var(--body-bg);
  background-image: url(../images/slider.jpg);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
	overflow: hidden;
  z-index: 2;
}
.home-slider {
  position: relative;
  min-height: 100vh;
  z-index: 5;
}
ul.home-slider {
  list-style: none;
  list-style-type: none;
  padding: 0;
}
.home-slider h1,
.home-slider h2,
.home-slider h3 {
  font-size: 3rem;
  color: var(--bold-color);
}
.home-slider p {
  display: inline-block;
  padding: 0 10px;
  font-size: 1.6rem;
  background: #ffffff;
  color: var(--bold-color);
}
.home-slider a.button {
  padding: 10px 20px;
  background: var(--primary);
  color: #ffffff;
  -webkit-transition: background 0.3s ease;
  -moz-transition: background 0.3s ease;
  transition: background 0.3s ease;
}
.home-slider .owl-item a.button:hover,
.home-slider a.button:hover {
  background: var(--bold-color);
}
.home-slider .owl-dots {
  position: relative;
  display: flex;
  justify-content: center;
  z-index: 15;
}
.home-slider .owl-dots button {
  width: 14px;
  height: 14px;
  border: 2px solid #ffffff;
  border-radius: 50%;
  margin: 0 4px;
  background: var(--bold-color);
}
.owl-dots button.active {
  background: var(--primary);
}
.owl-item {
  display: flex;
  align-items: center;
  height: 100vh;
}
.section,
.homepage-content {
  position: relative;
  z-index: 2;
}
.homepage-content .block {
  margin-bottom: 70px;
}
.homepage-content h3.block-title {
  margin-bottom: 20px;
  padding-bottom: 10px;
  font-size: 2rem;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
}
#block-countdowntimer-2 {
  h3.block-title {
    text-transform: inherit;
  }
}
.homepage-content h3.block-title::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 50%;
  width: 50px;
  height: 2px;
  margin-left: -25px;
  background: var(--primary);
}
.region-content-home {
  display: flex;
  flex-direction: column;
}
.views-element-container.contextual-region {
  .view-frontpage {
    display: none;
  }
}
.view-committee-s {
  .views-row {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    .views-col {
      min-width: 220px;
    }
  }
}
.slick-slide img {
  width: 100%;
  max-height: 600px;
}
#line-img {
  display: flex;
  justify-content: center;
  gap: 18px;
  img {
    max-width: 49%;
  }
}
#line-img-top {
  display: flex;
  justify-content: center;
  img {
    max-width: 100%;
  }
}
